import React, { useState,useContext } from 'react'
import { Link } from 'react-router-dom'
import {
  CCol,
  CContainer,
  CRow,
  CAlert,
  CProgress
} from '@coreui/react'


import LoginForm from './LoginForm';
import api from '../../../appstate/api';
import utils from '../../../appstate/utils';
import {AppContext} from '../../../appstate/appcontext'


const Login = (props) => {

  const { state,  dispatch } = useContext(AppContext)
  const [notify, setNotify] = useState(null)
  const [msg, setMsg] = useState(null)
  const [spin, setSpin] = useState(null)

  const [visible, setVisible] = React.useState(10)


  const onSubmit = (dd) => {
    
    setVisible(10)
    setNotify(false)
    setMsg('')
    setSpin(true)
    api.fxns.login(dd,api.fxns.endpoint).then(rd => {
          var out = rd;
          var token=out.tkn;
          if (out.success) {
            switch (out.sd.LST) {
              case "0":
                console.log("case 0");
                break;
              case "1":

                localStorage.setItem("out", JSON.stringify({ out }));
                localStorage.setItem("token", JSON.stringify({ token }));
                localStorage.setItem('votes',JSON.stringify({prz:'',vpz:'',sec:'',tre:'',prc:'',org:'',pro:'',wlf:'',woc:''}))
                document.cookie = "_inspire=" + token;
                props.history.push('/dashboard');
                // dispatch({type:'USER_LOGGED_IN',payload:utils.utilfxns.getmenus(out),initloading:false})
              default:
                if (out.st && out.sm) {
                  console.log("something else came up");
                }
            }
          } else {
            setNotify(true)
            setMsg(out.em)
            setSpin(false)
            document.cookie = "_inspire= ; expires = Thu, 01 Jan 1970 00:00:00 GMT"
            //dispatch({type:'NOTIFY',payload:out.em, login:true, title:out.et, initloading:false, ntype:'danger', time:7, place:'tc'})
          }
        },
        err => {
          dispatch({type:'NOTIFY',payload:err, login:true, title:'System error', initloading:false,ntype:'danger', time:7, place:'tc'})
        }
      );
    }
  return (
    <div className="c-app c-default-layout flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="8">
          { notify && msg && <CAlert
                color="danger"
                show={visible}
                closeButton
                onShowChange={setVisible}
              >
                { msg } {visible}
                <CProgress
                  striped
                  color="danger"
                  value={Number(visible) * 10}
                  size="xs"
                  className="mb-3"
                />
              </CAlert>}
            <LoginForm submit={onSubmit}/>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  )
}

export default Login
