import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react'


import utils from '../../../appstate/utils';

const LoginForm = ({ submit }) => {
  const [val, setValue]=useState({unm:'',pwd:''});

  const handleSubmit = (e) => {
    if(!val.unm || !val.pwd) return;
    var fm = new FormData()
    var cookie = utils.utilfxns.getcookie('_inspire');
    fm.append('unm',val.unm);fm.append('pwd',val.pwd);fm.append("s", "auth");
    fm.append("a", "authverify");fm.append("ssi", cookie);
    fm.append("ctx", "2");fm.append('m','l');

    submit(fm)
    //setValue({unm:'',pwd:''})
  }

  const onChange = e => setValue({...val, [e.target.name]: e.target.value})
  return (

    <CCardGroup>
        <CCard className="p-4">
        <CCardBody>
            <CForm>
            <h1>Login</h1>
            <p className="text-muted">Sign In to your account and vote</p>
            <CInputGroup className="mb-3">
                <CInputGroupPrepend>
                <CInputGroupText>
                    <CIcon name="cil-user" />
                </CInputGroupText>
                </CInputGroupPrepend>
                <CInput type="text" placeholder="Studentno" autoComplete="studentno" name='unm' value={val.unm} onChange={onChange} />
            </CInputGroup>
            <CInputGroup className="mb-4">
                <CInputGroupPrepend>
                <CInputGroupText>
                    <CIcon name="cil-lock-locked" />
                </CInputGroupText>
                </CInputGroupPrepend>
                <CInput type="password" placeholder="Password" autoComplete="current-password"  name='pwd' value={val.pwd} onChange={onChange}/>
            </CInputGroup>
            <CRow>
                <CCol xs="6">
                <CButton color="primary" className="px-4" onClick={handleSubmit}>Login</CButton>
                </CCol>
                <CCol xs="6" className="text-right">
                {/* <CButton color="link" className="px-0">Forgot password?</CButton> */}
                </CCol>
            </CRow>
            </CForm>
        </CCardBody>
        </CCard>
        <CCard className="text-white bg-white py-5 d-md-down-none" style={{ width: '44%' }}>
        <CCardBody className="text-center">
            <div>
            <img src={require('../../../assets/icons/vote.jpg')} width="300" height="320" alt="" srcSet=""/>
            {/* <h2>Sign up</h2>
            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
                labore et dolore magna aliqua.</p>
            <Link to="/register">
                <CButton color="primary" className="mt-3" active tabIndex={-1}>Register Now!</CButton>
            </Link> */}
            </div>
        </CCardBody>
        </CCard>
    </CCardGroup>

  )
}

export default LoginForm
